import React, { useState, useEffect } from 'react'
import {
    ChevronDown, Share, Printer, FileText, File, Grid, Copy, Plus, MoreVertical, Edit, Archive, Trash, Search, Eye, Lock, ChevronLeft, ChevronsLeft, User
} from 'react-feather'
import Avatar from '@components/avatar'
import {
    Card, CardHeader, CardTitle, Button, UncontrolledButtonDropdown, DropdownToggle, DropdownMenu, DropdownItem, Input, Label, Row, Col, Badge, Form, FormGroup, UncontrolledDropdown, Spinner, CardBody, TabContent, TabPane, Nav, NavItem, NavLink, Modal, ModalHeader, ModalBody, ModalFooter
} from 'reactstrap'
import useJwt from '@src/auth/jwt/useJwt'
import medal from '@src/assets/images/illustration/badge.svg'
import Media from 'reactstrap/lib/Media'
import CardText from 'reactstrap/lib/CardText'
import classnames from 'classnames'
import Barchart from './DashboardUI/Barchart'
import InvoiceTable from './DashboardUI/InvoiceTable'
import { CURRENCY_SYMBOL } from '../../Configuarables.js'
import PropertyBadge from '../../../assets/images/dashboardBadges/property.svg'
import branchBadge from '../../../assets/images/dashboardBadges/branch.svg'
import contractorBadge from '../../../assets/images/dashboardBadges/contractor.svg'
import landlordBadge from '../../../assets/images/dashboardBadges/landlord.svg'
import tenantBadge from '../../../assets/images/dashboardBadges/tenant.svg'
import EAdmin from '../../../assets/images/dashboardBadges/EAdmin.svg'

const Dashboard = () => {
    const [InvoiceDetails, setInvoiceDetails] = useState({})
    const [total_revenue, setTotal_revenue] = useState(0)
    const [counter, setCounter] = useState({
        active_estate_admin: 0,
        active_estate_manager: 0,
        block_estate_admin: 0,
        block_estate_manager: 0,
        contractor: 0,
        land_lord: 0,
        tenant: 0,
        due_amount: 0,
        received_amount: 0
    })
    useEffect(() => {
        useJwt.userCounter().then(res => {
            // console.log("userCounter", res.data.payload)
            const { active_estate_admin, active_estate_manager, block_estate_admin, block_estate_manager, contractor, land_lord, tenant, total_revenue, due_amount, received_amount } = res.data.payload
            setTotal_revenue(total_revenue)
            setCounter({ active_estate_admin, active_estate_manager, block_estate_admin, block_estate_manager, contractor, land_lord, tenant, due_amount, received_amount })
        }).catch(err => {
            console.log(err)
        })
    }, [])
    const data = [
        {
            title: Number(counter.active_estate_admin) + Number(counter.block_estate_admin),
            subtitle: 'Estate Admins',
            color: 'light-primary',
            icon: EAdmin
        },
        {
            title: Number(counter.active_estate_manager) + Number(counter.block_estate_manager),
            subtitle: 'Branch Managers',
            color: 'light-warning',
            icon: branchBadge
        },
        {
            title: counter.contractor,
            subtitle: 'Contractors',
            color: 'light-info',
            icon: contractorBadge
        },
        {
            title: counter.tenant,
            subtitle: 'Tenants',
            color: 'light-danger',
            icon: tenantBadge
        },
        {
            title: counter.land_lord,
            subtitle: 'Landlords',
            color: 'light-success',
            icon: landlordBadge
        }
    ]
    return (
        <>

            <Row className='match-height'>
                <Col md='4'>
                    <Card className='card-congratulations-medal'>
                        <CardBody>
                            <h5>Total Revenue</h5>
                            {/* <CardText className='font-small-3'>You have won gold medal</CardText> */}
                            <h3 className='mb-75 mt-2 pt-50'>
                                <a href='/' onClick={e => e.preventDefault()}>
                                    {CURRENCY_SYMBOL} {total_revenue}
                                </a>
                            </h3>
                            {/* <Button.Ripple color='primary'>View Sales</Button.Ripple> */}
                            <img className='congratulation-medal' src={medal} alt='Medal Pic' height={80} />
                            <Row className='mt-3'>
                                <Col md='6'>
                                    <p><b>Received</b></p>
                                    <p className="text-success">
                                        <b>{CURRENCY_SYMBOL} {counter.received_amount}</b>
                                    </p>
                                </Col>
                                <Col md='6'>
                                    <p><b>Due Amount</b></p>
                                    <p className="text-warning">
                                        <b> {CURRENCY_SYMBOL} {counter.due_amount}</b>
                                    </p>
                                </Col>
                            </Row>
                        </CardBody>
                    </Card>
                </Col>

                <Col md='8'>
                    <Card className='card-statistics'>
                        <CardHeader>
                            <CardTitle tag='h5'>Users</CardTitle>
                            {/* <CardText className='card-text font-small-2 mr-25 mb-0'>Updated 1 month ago</CardText> */}
                        </CardHeader>
                        <CardBody className='px-1'>
                            <div className='row'>{
                                data.map((item, index) => {
                                    return (
                                        <Col sm='4'>
                                            <Media>
                                                {/* <Avatar color={item.color} icon={item.icon} className='mb-1 mr-1' /> */}
                                                <img src={item.icon} className='img-fluid m-1' />
                                                <Media className='my-auto' body>
                                                    <h4 className='font-weight-bolder mb-0'>{item.title}</h4>
                                                    <CardText className='font-small-2 mb-0'>{item.subtitle}</CardText>
                                                </Media>
                                            </Media>
                                        </Col>
                                    )
                                })
                            }</div>
                        </CardBody>
                    </Card>
                </Col>
            </Row>
            <Barchart />
            <InvoiceTable />
        </>
    )
}

export default Dashboard