import React, { useState, useEffect } from 'react'
import {
    ChevronDown, Share, Printer, FileText, File, Grid, Copy, Plus, MoreVertical, Edit, Archive, Trash, Search, Eye, Lock, ChevronLeft, ChevronsLeft, ChevronsRight
} from 'react-feather'
import {
    Card, CardHeader, CardTitle, Button, Table, DropdownToggle, DropdownMenu, DropdownItem, Input, Label, Row, Col, Badge, Form, FormGroup, UncontrolledDropdown, Spinner, CardBody, TabContent, TabPane, Nav, NavItem, NavLink, Modal, ModalHeader, ModalBody, ModalFooter
} from 'reactstrap'
import { CURRENCY_SYMBOL } from '../../../Configuarables'
import { formatReadableDate } from '../../../helper'

const InvoiceModal = ({ InvoiceDetails, InvModal, setInvModal }) => {
    useEffect(() => {
        console.log('row', InvoiceDetails)
    }, [])
    return (
        <Modal
            isOpen={InvModal}
            toggle={() => setInvModal(!InvModal)}
            className={`modal-dialog-centered modal-xl`}
        >
            <ModalHeader toggle={() => setInvModal(!InvModal)}></ModalHeader>
            <ModalBody>
                <Card>
                    <br />
                    <h3 className="text-primary text-center"> <b> Invoice Details</b></h3>
                    <br />
                    <div className="text-center">
                        <Badge pill color={'warning'} className='px-2 py-1'>
                            Current Status : {InvoiceDetails.statusType.status_name}
                        </Badge>
                    </div>
                    <br />
                    <Table responsive bordered >
                        <thead >
                            <tr >
                                <th className='bg-white'>Invoice ID</th>
                                <th className='bg-white'>Issue Date</th>
                                <th className='bg-white'>Total Amount</th>
                                <th className='bg-white'>Paid Amount</th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr>
                                <th scope="row">{InvoiceDetails.invoice_id}</th>
                                <td>{formatReadableDate(InvoiceDetails.issue_date)}</td>
                                <td>{CURRENCY_SYMBOL} {InvoiceDetails.total_amount}</td>
                                <td>{CURRENCY_SYMBOL} {InvoiceDetails.pay_amount}</td>
                            </tr>
                        </tbody>
                    </Table>
                    <br />
                    <br />
                    <Row className='px-md-5 px-2'>
                        <Col md='6' className='pl-2'>
                            <p><b>Client Info</b></p>
                            <p>Name :&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{InvoiceDetails.invoice_info.clientinfo.name}</p>
                            <p>Phone :&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{InvoiceDetails.invoice_info.clientinfo.phone}</p>
                            <p>Property :&nbsp;&nbsp;&nbsp;{InvoiceDetails.invoice_info.clientinfo.property}</p>
                            <p>Email :&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{InvoiceDetails.invoice_info.clientinfo.email}</p>
                        </Col>
                        <Col md='6' className='pl-2'>
                            <p><b>Merchant Info</b></p>
                            <p>Name :&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{InvoiceDetails.invoice_info.merchantinfo.name}</p>
                            <p>Phone :&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{InvoiceDetails.invoice_info.merchantinfo.phone}</p>
                            <p>Address :&nbsp;&nbsp;&nbsp;&nbsp;{InvoiceDetails.invoice_info.merchantinfo.address}</p>
                            <p>Email :&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{InvoiceDetails.invoice_info.merchantinfo.email}</p>
                        </Col>
                    </Row>
                    <br />
                    <div className="text-center">
                        <Badge pill color={'primary'} className='px-3 py-1'>
                            Payment Info
                        </Badge>
                    </div>
                    <br />
                    <Table responsive bordered >
                        <thead >
                            <tr >
                                <th className='bg-white'>Tenants</th>
                                <th className='bg-white'>Description</th>
                                <th className='bg-white'>Payable for Month</th>
                                <th className='bg-white'>Per Unit</th>
                                {/* <th className='bg-white'>Quantity</th> */}
                                <th className='bg-white'>Amount</th>
                            </tr>
                        </thead>
                        <tbody>
                            {
                                InvoiceDetails.invoice_info.iteminfo.map(x => <tr>
                                    <th scope="row">{x.number_of_renter || "0"}</th>
                                    <td>{x.description}</td>
                                    <td>{x.pay_for_date}</td>
                                    <td>{x.unit_per}</td>
                                    {/* <td>{x.number_of_renter}</td> */}
                                    <td>{x.amount}</td>
                                </tr>)
                            }
                        </tbody>
                    </Table>
                    <div className="d-flex justify-content-end">
                        <div className='py-3 pr-2'>
                            <p className='border-bottom p-1 m-0'><b>Sub-Total</b> :&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{CURRENCY_SYMBOL} {InvoiceDetails.invoice_info.amountinfo.sub_total}</p>
                            <p className='border-bottom p-1 m-0'><b>Tax</b> :&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{CURRENCY_SYMBOL} {InvoiceDetails.invoice_info.amountinfo.tax} </p>
                            <p className='bg-primary text-white p-1 m-0'><b>Total</b> :&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{CURRENCY_SYMBOL} {InvoiceDetails.invoice_info.amountinfo.total}</p>
                        </div>
                    </div>
                </Card>
            </ModalBody>
        </Modal>
    )
}

export default InvoiceModal